body {
  background-color: #f3f3f3;
}
.App {
  text-align: center;
  padding-top: 20px;
  padding-bottom: 30px;
  max-width: 600px;
  min-width: 400px;
  margin: 0 auto;
  background-color: white;
  border-top: none;
  border-radius: 0 0 5px 5px;
  box-shadow: #f3f3f3;
  margin-bottom: 40px;
}

strong {
  font-weight: 800;
  font-size: 1.7em;
  color: rgb(48, 58, 166);
}

label {
  color: rgb(48, 58, 166) !important;
  font-weight: 800 !important;
}
